.navbar{width:100%;background-color:#ccc;color:#252525;padding:30px}.navbar .ali{padding:10px;text-decoration:none;color:#000}.navbar .activator{font-size:30px;margin:10px 30px 0 0;color:#000;background:transparent;border:none;cursor:pointer;outline:none;float:right}.navbar .logo{padding:10px;text-decoration:none;color:#000}.navbar .links{height:0;overflow:hidden;list-style-type:none;font-weight:bold;vertical-align:top}.navbar .show-nav{height:120px}@media screen and (min-width: 768px){.navbar .activator{display:none}.navbar .links{height:auto;display:inline-flex;margin:0}.navbar .ali{padding:0px}.navbar .ali.first{margin-right:20px}}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

